.scrm-player-body {border: 1px solid #e9ecef; border-radius: 0.3em; padding:5px; overflow: auto; height: 300px;}

.scrm-player-kewords-list {text-align: start; padding:10px; font-size:14px; background: #f8f9fa; border-radius: 0.3em; min-height: 20px;}
.scrm-player-kewords-list-item {width: auto; height: 24px;line-height: 24px; display: inline-block;  font-size: 14px;  padding:0 5px;}
.scrm-player-kewords-list-item.default{color: black}
.scrm-player-kewords-list-item.warn{color: red}
.scrm-player-kewords-list-item.issue{color: grey}

.scrm-player-waveform-div {height: 60px; width: 100%; overflow: hidden;}
.scrm-player-playbar {width: 100%; height:8px;  border-radius: 25%;	-webkit-appearance: none; background: #f1f3f5; border:0; margin: 15px 0 20px;}
.scrm-player-playbar::-webkit-slider-thumb {
	-webkit-appearance: none;
	position: relative;
	height:16px; width:16px;
	background:#3b5bdb;
	border-radius: 50%;
	-webkit-transition: all 0.2s;
	transition: all 0.2s;
	}
	.scrm-player-playbar::-webkit-slider-thumb:hover { height:20px; width:20px; margin-left: -2px;}
.scrm-player-volumebar {vertical-align: 'bottom'; width:84px; margin-left: 28px; top: 5px; position: relative;}
.scrm-player-volumebar:before {content: '\ea5b';font-family: 'xeicon' !important; position: absolute; left:-22px; font-size:1.4em; color:#868e96;}
.scrm-player-volumebar {-webkit-appearance: none; height:18px; background: #f1f3f5; border-radius: 9px;}
.scrm-player-volumebar:focus {outline: none;}
.scrm-player-volumebar::range-progress { 
	background: #3071a9; 
  } 

.scrm-player-volumebar::-ms-track { width: 100%; height:5px; cursor: pointer;  background: transparent; border-color: transparent;   color: transparent; }
.scrm-player-volumebar::-webkit-slider-thumb { 
	-webkit-appearance: none;
	background: #adb5bd;
	cursor: pointer; 
	height: 14px; width: 14px;   
	margin-top: -0px;  
	border-radius: 50%;
  }
.scrm-player-answer-list-area{overflow: auto; height: 40vh;}
.scrm-player-answer-list-div{width: 98%; height: 36px;}
.scrm-player-answer-list-div:after {content: ''; display: block; float: none; clear: both;}
.scrm-player-answer-list-div > * {float: left;}
.scrm-player-answer-list-div > .scrm-rfloat-div {float: right;}
.scrm-player-answer-list-div > textarea {height:27px; width:225px; border:1px solid #ddd;}
.scrm-player-answer-list-div > .scrm-profile {width:30px; }
.scrm-player-answer-list-div > .scrm-profile i {font-size:1.6em;line-height: 34px; color:#ced4da;  }

.scrm-player-answer-list-tail {text-align: center; padding: 5px 0 15px;}
.scrm-player-answer-list-tail > button {width:24px; border-radius: 50%;  }

.scrm-switch-label {position: relative; display: inline-block; width: 40px; height: 24px; top:-2px; margin-left: 10px;}
.scrm-switch-input {opacity: 0; width: 0; height: 0; }
.scrm-switch-input:checked + .scrm-switch-span {background-color: #12b886;} 
.scrm-switch-input:checked + .scrm-switch-span:before { transform: translateX(14px);}
.scrm-switch-span {position: absolute; cursor: pointer; top: 0; left: 0; right: 0; bottom: 0; background-color: #dee2e6; transition: .4s; border-radius: 34px;}
.scrm-switch-span:before {position: absolute; content: ""; height: 18px; width: 18px; left: 4px; bottom: 3px; background-color: white; transition: .4s; border-radius: 50%;}

#nprogress .bar {background: red !important;}
#nprogress .peg {box-shadow: 0 0 10px red, 0 0 5px red !important;}
#nprogress .spinner-icon {border-top-color: red !important; border-left-color: red !important;}

