.dzu-inputLabel:before {content: '\eb7b'; font-family: 'xeicon' !important; font-weight: 100; display: block; position: absolute;  top: 25px; font-size:30px;}
.dzu-inputLabelWithFiles {margin:30px; border-radius: 2px; background: #F8F9FA; border:1px solid #CED4DA; color:#949DA8;}
.dzu-inputLabelWithFiles:hover, .dzu-inputLabelWithFiles:focus, .dzu-inputLabelWithFiles:active {background: #CED4DA; color:#212529;}

.dzu-submitButtonContainer {margin:0 auto; position:absolute; bottom:30px; right:30px;}
.dzu-submitButton {border-radius: 2px; color: #ffffff; background: #3B5BDB;} 
.dzu-submitButton:hover, .dzu-submitButton:focus, .dzu-submitButton:active {background-color: #364FC7; }
.dzu-previewContainer {padding:0px; height: 40px; min-height: 40px;}
.dzu-previewFileName {padding-left:20px;}
.dzu-previewStatusContainer {padding-right:30px;}
.dzu-previewStatusContainer .dzu-previewButton {margin: 0 0 0 10px;}
