progress {
	background-color: #f3f3f3;
	border: 0; 
	height: 10px;
	border-radius: 5px;
}
progress::-webkit-progress-bar {
	background-color: #f3f3f3;
	border-radius: 9px;
}
progress::-webkit-progress-value {
	background: #7048e8;
	border-radius: 9px;
}
progress::-moz-progress-bar {
	background: #7048e8;
	border-radius: 9px;
}


